@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap');

@import url('https://fonts.googleapis.com/icon?family=Material+Icons');

$buttonHeight: 70px;
$buttonWidth: 70px;

body {
  user-select: none;
  font-family: Roboto; 
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  height: 100vh;
}

.container {
  i{
  padding: 5px;
  }
  h2{
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    top: 40px;
    text-align: center;
    color: #909090;
    width: fit-content;
  }
  a{
    text-decoration: none;
  }
}
body{
  background-image: linear-gradient(to bottom, #FFFFFF , #DBEEFF);
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: default;
}

.calculator {
  position: relative;
  height: $buttonHeight * 6.5;
  background-color: white;
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  max-width: 350px;
  box-shadow: inset 12px 12px 24px #F1F8FF;
  box-shadow: inset -12px -12px 24px #B7D9FA;
  filter: drop-shadow(12px 12px 12px #9EC1E1); 
  border-radius: 25px;
   overflow: auto;
}
.screen {
  background-color: rgba(0,0,0,.15);
  margin: 20px 20px 10px 20px;
  padding: 5px 10px 0px 0px;
  border-radius: 8px;
}

  #display{
    text-align: right;
    font-size: 20px;
    height: 30px;
    color: black;
  }

.formulaDisplay{
  font-size: 16px;
  text-align: right;
  color: grey;
  height: 20px;
}

.button-container {
  margin: 0px 20px 20px 20px;
}
button {
  position: relative;
  float: left;
  background-color: white;
  color: black;
  border-radius: 10px;
  width: $buttonWidth;
  height: $buttonHeight;
  box-sizing: border-box;
  outline: 0px solid grey;
  cursor: pointer;
  &:hover {
     box-shadow: inset 2px 2px 2px black;
  }
}

.buttonOperator {
  background-color: rgba(0,0,0,.3)
}
.buttonClear {
  background-color: pink;
  width: $buttonWidth*2;
}
.buttonEquals{
  position: absolute;
  background-color: #9EC1E1;
  height: $buttonHeight*2;
  bottom: 20px;
  right: 20px;
}
.largeButton {
  width: 140px;
}

@media screen and (max-width: 550px) {
  .container {
    i{
    }
    h2{
      top: 20px;
    }
    a{
      text-decoration: none;
    }
  }
}